import React, { useEffect, useMemo, useRef, useState, useId, KeyboardEvent } from "react";
import { Link, useSearchParams } from "react-router-dom";
import api from "../services/api.ts";
import { useClub } from "../hooks/useClub.tsx";

// ===== Tipos =====
export interface CalendarDaySummaryDto {
    date: string;
    matchesCount: number;
    wins: number;
    draws: number;
    losses: number;
    goalsFor: number;
    goalsAgainst: number;
}
export interface CalendarMonthDto {
    year: number;
    month: number;
    days: CalendarDaySummaryDto[];
}
export interface CalendarMatchStatLineDto {
    totalGoals: number;
    totalAssists: number;
    totalShots: number;
    totalPassesMade: number;
    totalPassAttempts: number;
    totalTacklesMade: number;
    totalTackleAttempts: number;
    totalRedCards: number;
    totalSaves: number;
    totalMom: number;
    passAccuracyPercent: number;
    tackleSuccessPercent: number;
    goalAccuracyPercent: number;
    avgRating: number;
}
export interface CalendarMatchSummaryDto {
    matchId: number;
    timestamp: string;
    clubAId: number;
    clubAName: string;
    clubAGoals: number;
    clubACrestAssetId?: string | null;
    clubBId: number;
    clubBName: string;
    clubBGoals: number;
    clubBCrestAssetId?: string | null;
    resultForClub: "W" | "D" | "L" | "-";
    stats: CalendarMatchStatLineDto;
}
export interface CalendarDayDetailsDto {
    date: string;
    totalMatches: number;
    wins: number;
    draws: number;
    losses: number;
    goalsFor: number;
    goalsAgainst: number;
    matches: CalendarMatchSummaryDto[];
}

// ===== Helpers =====
const ptMonth = new Intl.DateTimeFormat("pt-BR", { month: "long" });
const ptWeekday = new Intl.DateTimeFormat("pt-BR", { weekday: "short" });
const ptDay = new Intl.DateTimeFormat("pt-BR", { day: "2-digit" });
const ptTime = new Intl.DateTimeFormat("pt-BR", { hour: "2-digit", minute: "2-digit" });

const pad = (n: number) => (n < 10 ? `0${n}` : `${n}`);
const toYmd = (d: Date) => `${d.getFullYear()}-${pad(d.getMonth() + 1)}-${pad(d.getDate())}`;
const fromYmd = (s: string) => { const [y, m, d] = s.split("-").map(Number); return new Date(y, m - 1, d); };
function startOfMonth(d: Date) { return new Date(d.getFullYear(), d.getMonth(), 1); }
function addMonths(d: Date, months: number) { return new Date(d.getFullYear(), d.getMonth() + months, 1); }
function addDays(d: Date, days: number) { const nd = new Date(d); nd.setDate(nd.getDate() + days); return nd; }
function getGridStart(date: Date) { const first = startOfMonth(date); const dow = (first.getDay() + 6) % 7; const gridStart = new Date(first); gridStart.setDate(first.getDate() - dow); return gridStart; }
function getWeekStart(d: Date) { const dow = (d.getDay() + 6) % 7; const ws = new Date(d); ws.setDate(d.getDate() - dow); return new Date(ws.getFullYear(), ws.getMonth(), ws.getDate()); }
const crestUrl = (id?: string | null) => id ? `https://eafc24.content.easports.com/fifa/fltOnlineAssets/24B23FDE-7835-41C2-87A2-F453DFDB2E82/2024/fcweb/crests/256x256/l${id}.png` : null;

// ===== UI =====
function ResultPill({ r }: { r: "W" | "D" | "L" | "-" }) {
    const map: Record<string, string> = {
        W: "bg-green-100 text-green-700 border-green-200",
        D: "bg-yellow-100 text-yellow-700 border-yellow-200",
        L: "bg-red-100 text-red-700 border-red-200",
        "-": "bg-gray-100 text-gray-600 border-gray-200",
    };
    const label = r === "W" ? "Vitória" : r === "D" ? "Empate" : r === "L" ? "Derrota" : "-";
    return <span className={`px-2 py-0.5 rounded-full text-xs border ${map[r]}`}>{label}</span>;
}
function Crest({ id, alt }: { id?: string | null; alt: string }) {
    const url = crestUrl(id);
    if (!url) return <div className="w-6 h-6 rounded-full bg-gray-100" aria-hidden />;
    return (
        <img src={url} alt={alt} className="w-6 h-6 rounded-full bg-gray-100 object-contain" loading="lazy" decoding="async" />
    );
}
function Skeleton({ className = "" }: { className?: string }) { return <div className={`animate-pulse bg-gray-200 rounded ${className}`} />; }

function Dots({ count }: { count: number }) {
    const capped = Math.min(4, count);
    return (
        <div className="absolute bottom-1 left-1 right-1 flex justify-center gap-1">
            {Array.from({ length: capped }).map((_, i) => (
                <span key={i} className="w-1.5 h-1.5 rounded-full bg-blue-400/70" />
            ))}
        </div>
    );
}

// ===== Página =====
type ViewMode = "monthly" | "weekly";

export default function CalendarPage() {
    const { club } = useClub();
    const [searchParams, setSearchParams] = useSearchParams();

    // 🔹 Suporta seleção múltipla via URL (?clubIds=1,2,3) OU um único via context (?clubId)
    const urlClubIds = searchParams.get("clubIds");
    const parsedClubIds = useMemo(
        () => (urlClubIds ? urlClubIds.split(",").map(s => parseInt(s, 10)).filter(n => Number.isFinite(n)) : []),
        [urlClubIds]
    );
    const singleClubId = club?.clubId ?? null;
    const selectedClubIds = parsedClubIds.length ? parsedClubIds : (singleClubId ? [singleClubId] : []);
    const hasAnyClub = selectedClubIds.length > 0;
    const clubKey = hasAnyClub ? selectedClubIds.join(",") : null; // usado como dependência estável

    const clubName = club?.clubName ?? "";
    const headerLabel = hasAnyClub
        ? (selectedClubIds.length === 1 ? `${clubName || selectedClubIds[0]} (${selectedClubIds[0]})` : `${selectedClubIds.length} clubes`)
        : "-";

    // Estado de visualização
    const [viewMode, setViewMode] = useState<ViewMode>("monthly");

    // Âncoras de navegação
    const [referenceMonth, setReferenceMonth] = useState<Date>(startOfMonth(new Date()));
    const [referenceWeekStart, setReferenceWeekStart] = useState<Date>(getWeekStart(new Date()));

    // Dados / estados
    const [monthData, setMonthData] = useState<CalendarMonthDto | null>(null);
    const [loadingMonth, setLoadingMonth] = useState(false);
    const [errorMonth, setErrorMonth] = useState<string | null>(null);

    const [selectedDate, setSelectedDate] = useState<string | null>(null);
    const [dayData, setDayData] = useState<CalendarDayDetailsDto | null>(null);
    const [loadingDay, setLoadingDay] = useState(false);
    const [errorDay, setErrorDay] = useState<string | null>(null);

    const [lastActiveButton, setLastActiveButton] = useState<HTMLButtonElement | null>(null);
    const dialogTitleId = useId();

    const monthCacheRef = useRef<Record<string, CalendarMonthDto>>({});
    const dayCacheRef = useRef<Record<string, CalendarDayDetailsDto>>({});

    const pageRef = useRef<HTMLDivElement | null>(null);
    const headerRef = useRef<HTMLDivElement | null>(null);
    const legendRef = useRef<HTMLDivElement | null>(null);
    const [gridHeight, setGridHeight] = useState<number>(520);

    const year = referenceMonth.getFullYear();
    const month1to12 = referenceMonth.getMonth() + 1;
    const monthKey = `${year}-${pad(month1to12)}`;

    const todayYmd = toYmd(new Date());

    const weekdays = useMemo(() => {
        const base = new Date(2023, 0, 2);
        return Array.from({ length: 7 }, (_, i) => { const d = new Date(base); d.setDate(base.getDate() + i); return ptWeekday.format(d).toUpperCase(); });
    }, []);

    const gridDays = useMemo(() => {
        if (viewMode === "monthly") {
            const start = getGridStart(referenceMonth);
            const totalCells = 42;
            return Array.from({ length: totalCells }, (_, i) => { const d = new Date(start); d.setDate(start.getDate() + i); return d; });
        }
        return Array.from({ length: 7 }, (_, i) => addDays(referenceWeekStart, i));
    }, [referenceMonth, referenceWeekStart, viewMode]);

    const summaryByDate: Record<string, CalendarDaySummaryDto> = useMemo(() => {
        const map: Record<string, CalendarDaySummaryDto> = {};
        for (const s of monthData?.days ?? []) map[s.date] = s;
        return map;
    }, [monthData]);

    const isSameMonth = (d: Date, ref: Date) => d.getMonth() === ref.getMonth() && d.getFullYear() === ref.getFullYear();

    useEffect(() => {
        setErrorMonth(null);
        setSelectedDate(null);
        setDayData(null);
        setErrorDay(null);
    }, [clubKey, year, month1to12]);

    useEffect(() => {
        function recompute() {
            const vh = window.innerHeight;
            const headerH = headerRef.current?.getBoundingClientRect().height ?? 0;
            const legendH = legendRef.current?.getBoundingClientRect().height ?? 0;
            const paddingY = 32;
            const guard = 8;
            const available = Math.max(280, Math.floor(vh - headerH - legendH - paddingY - guard));
            setGridHeight(available);
        }
        recompute();
        window.addEventListener("resize", recompute);
        return () => window.removeEventListener("resize", recompute);
    }, [viewMode]);

    // ===== Ler do URL no mount =====
    const didInitFromUrlRef = useRef(false);
    useEffect(() => {
        if (didInitFromUrlRef.current) return;
        didInitFromUrlRef.current = true;

        const modeParam = searchParams.get("mode");
        const ymYear = Number(searchParams.get("year"));
        const ymMonth = Number(searchParams.get("month"));
        const weekStartParam = searchParams.get("weekStart");
        const dateParam = searchParams.get("date");

        if (modeParam === "weekly" || modeParam === "monthly") {
            setViewMode(modeParam);
        }

        if (modeParam === "weekly" && weekStartParam) {
            const ws = fromYmd(weekStartParam);
            const norm = getWeekStart(ws);
            setReferenceWeekStart(norm);
            setReferenceMonth(startOfMonth(norm));
        } else if (Number.isFinite(ymYear) && Number.isFinite(ymMonth) && ymYear && ymMonth) {
            const m = new Date(ymYear, ymMonth - 1, 1);
            setReferenceMonth(startOfMonth(m));
            setReferenceWeekStart(getWeekStart(m));
        }

        if (dateParam) setSelectedDate(dateParam);
    }, [searchParams]);

    // ===== Escrever no URL quando estado muda (preservando clubIds / clubId) =====
    useEffect(() => {
        const next = new URLSearchParams(searchParams.toString());
        next.set("mode", viewMode);

        if (viewMode === "monthly") {
            next.set("year", String(referenceMonth.getFullYear()));
            next.set("month", String(referenceMonth.getMonth() + 1));
            next.delete("weekStart");
        } else {
            next.set("weekStart", toYmd(referenceWeekStart));
            next.delete("year");
            next.delete("month");
        }

        if (selectedDate) next.set("date", selectedDate);
        else next.delete("date");

        const prevStr = searchParams.toString();
        const nextStr = next.toString();
        if (prevStr !== nextStr) setSearchParams(next, { replace: true });
    }, [viewMode, referenceMonth, referenceWeekStart, selectedDate, searchParams, setSearchParams]);

    // ===== Buscar mês (com cache) + prefetch =====
    useEffect(() => {
        if (!hasAnyClub || !clubKey) return;
        let disposed = false;

        async function fetchMonth(y: number, m: number, write = true) {
            const k = `${y}-${pad(m)}|${clubKey}`;
            if (monthCacheRef.current[k]) { if (!disposed && write) setMonthData(monthCacheRef.current[k]); return; }
            if (write) { setLoadingMonth(true); setErrorMonth(null); setMonthData(null); }
            try {
                const params: any = { year: y, month: m };
                if (selectedClubIds.length > 1) params.clubIds = clubKey;
                else params.clubId = selectedClubIds[0];

                const { data } = await api.get<CalendarMonthDto>(
                    "https://eafctracker-cvadcceuerbgegdj.brazilsouth-01.azurewebsites.net/api/Calendar",
                    { params }
                );
                monthCacheRef.current[k] = data;
                if (!disposed && write) setMonthData(data);
            } catch (err: any) {
                if (!disposed && write) setErrorMonth(err?.message ?? "Erro ao carregar calendário");
            } finally {
                if (!disposed && write) setLoadingMonth(false);
            }
        }

        fetchMonth(year, month1to12, true);
        const prev = addMonths(referenceMonth, -1); fetchMonth(prev.getFullYear(), prev.getMonth() + 1, false);
        const next = addMonths(referenceMonth, 1); fetchMonth(next.getFullYear(), next.getMonth() + 1, false);

        return () => { disposed = true; };
    }, [hasAnyClub, clubKey, selectedClubIds, year, month1to12, referenceMonth]);

    // ===== Buscar dia (com cache) =====
    useEffect(() => {
        if (!selectedDate || !hasAnyClub || !clubKey) return;
        let disposed = false;

        async function run() {
            const cacheKey = `${selectedDate}|${clubKey}`;
            const cached = dayCacheRef.current[cacheKey];
            if (cached) { if (!disposed) setDayData(cached); return; }

            setLoadingDay(true); setErrorDay(null); setDayData(null);
            try {
                const params: any = { date: selectedDate };
                if (selectedClubIds.length > 1) params.clubIds = clubKey;
                else params.clubId = selectedClubIds[0];

                const { data } = await api.get<CalendarDayDetailsDto>(
                    "https://eafctracker-cvadcceuerbgegdj.brazilsouth-01.azurewebsites.net/api/Calendar/day",
                    { params }
                );
                dayCacheRef.current[cacheKey] = data;
                if (!disposed) setDayData(data);
            } catch (err: any) {
                if (!disposed) setErrorDay(err?.message ?? "Erro ao carregar o dia");
            } finally {
                if (!disposed) setLoadingDay(false);
            }
        }
        run();
        return () => { disposed = true; };
    }, [selectedDate, hasAnyClub, clubKey, selectedClubIds]);

    // Navegação por teclado
    function handleKeyNav(e: KeyboardEvent<HTMLDivElement>) {
        if (e.altKey || e.ctrlKey || e.metaKey) return;
        if (e.key === "ArrowLeft") {
            e.preventDefault();
            if (viewMode === "monthly") setReferenceMonth(addMonths(referenceMonth, -1));
            else {
                const newStart = addDays(referenceWeekStart, -7);
                setReferenceWeekStart(newStart);
                setReferenceMonth(startOfMonth(newStart));
            }
        } else if (e.key === "ArrowRight") {
            e.preventDefault();
            if (viewMode === "monthly") setReferenceMonth(addMonths(referenceMonth, 1));
            else {
                const newStart = addDays(referenceWeekStart, 7);
                setReferenceWeekStart(newStart);
                setReferenceMonth(startOfMonth(newStart));
            }
        } else if (e.key.toLowerCase() === "t") {
            const today = new Date();
            setReferenceMonth(startOfMonth(today));
            setReferenceWeekStart(getWeekStart(today));
        } else if (e.key.toLowerCase() === "m") {
            setViewMode("monthly");
        } else if (e.key.toLowerCase() === "s") {
            setViewMode("weekly");
        }
    }

    // Título da semana
    const weekTitle = useMemo(() => {
        const start = referenceWeekStart; const end = addDays(start, 6);
        const sameMonthYear = start.getMonth() === end.getMonth() && start.getFullYear() === end.getFullYear();
        if (sameMonthYear) return `${ptDay.format(start)}–${ptDay.format(end)} de ${ptMonth.format(start)} de ${start.getFullYear()}`;
        return `${ptDay.format(start)} ${ptMonth.format(start)} ${start.getFullYear()} – ${ptDay.format(end)} ${ptMonth.format(end)} ${end.getFullYear()}`;
    }, [referenceWeekStart]);

    function cellTone(count?: number) {
        if (!count || count <= 0) return "";
        if (count >= 4) return "bg-blue-50";
        if (count === 3) return "bg-indigo-50";
        if (count === 2) return "bg-violet-50";
        return "bg-purple-50";
    }

    const ROWS = viewMode === "monthly" ? 6 : 1;
    const rowGapPx = (ROWS - 1) * 8;
    const minRow = viewMode === "monthly" ? 56 : 88;
    const rowHeight = Math.max(minRow, Math.floor((gridHeight - rowGapPx) / ROWS));

    return (
        <div ref={pageRef} className="p-4 max-w-6xl mx-auto min-h-[100dvh]" onKeyDown={handleKeyNav} aria-live="polite">
            {/* Header */}
            <div ref={headerRef} className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3 mb-3">
                <div className="flex items-center gap-2">
                    <button onClick={() => { if (viewMode === "monthly") setReferenceMonth(addMonths(referenceMonth, -1)); else { const newStart = addDays(referenceWeekStart, -7); setReferenceWeekStart(newStart); setReferenceMonth(startOfMonth(newStart)); } }} className="px-3 py-2 rounded-lg border bg-white hover:bg-gray-50" aria-label={viewMode === "monthly" ? "Mês anterior" : "Semana anterior"}>◀</button>
                    <button onClick={() => { const today = new Date(); setReferenceMonth(startOfMonth(today)); setReferenceWeekStart(getWeekStart(today)); }} className="px-3 py-2 rounded-lg border bg-white hover:bg-gray-50">Hoje</button>
                    <button onClick={() => { if (viewMode === "monthly") setReferenceMonth(addMonths(referenceMonth, 1)); else { const newStart = addDays(referenceWeekStart, 7); setReferenceWeekStart(newStart); setReferenceMonth(startOfMonth(newStart)); } }} className="px-3 py-2 rounded-lg border bg-white hover:bg-gray-50" aria-label={viewMode === "monthly" ? "Próximo mês" : "Próxima semana"}>▶</button>

                    <h1 className="text-2xl font-bold ml-2">{viewMode === "monthly" ? `${ptMonth.format(referenceMonth)} de ${referenceMonth.getFullYear()}` : `Semana: ${weekTitle}`}</h1>
                </div>

                <div className="flex items-center gap-3">
                    <div className="text-sm text-gray-600">
                        Clube atual: <span className="font-semibold">{headerLabel}</span>
                    </div>
                    <div role="tablist" aria-label="Modo de visualização" className="inline-flex rounded-lg border overflow-hidden">
                        <button role="tab" aria-selected={viewMode === "monthly"} onClick={() => setViewMode("monthly")} className={`px-3 py-1.5 text-sm ${viewMode === "monthly" ? "bg-blue-600 text-white" : "bg-white text-gray-700 hover:bg-gray-50"}`}>Mensal</button>
                        <button role="tab" aria-selected={viewMode === "weekly"} onClick={() => setViewMode("weekly")} className={`px-3 py-1.5 text-sm border-l ${viewMode === "weekly" ? "bg-blue-600 text-white" : "bg-white text-gray-700 hover:bg-gray-50"}`}>Semanal</button>
                    </div>
                </div>
            </div>

            {/* Aviso quando não há clube definido */}
            {!hasAnyClub && (
                <div className="p-4 bg-yellow-50 border border-yellow-200 rounded text-yellow-800">
                    Defina um <b>clubId</b> ou <b>clubIds</b> no topo para visualizar o calendário.
                </div>
            )}

            {/* Legenda/ajuda */}
            <div ref={legendRef} className="flex items-center gap-3 text-xs text-gray-600 mt-3 mb-2">
                <span className="inline-flex items-center gap-1"><span className="w-2 h-2 rounded-full bg-blue-400/70" />Partidas no dia</span>
                <span className="px-1 rounded bg-green-100 text-green-700">V</span>
                <span className="px-1 rounded bg-yellow-100 text-yellow-700">E</span>
                <span className="px-1 rounded bg-red-100 text-red-700">D</span>
                <span className="ml-auto">Atalhos: ← → navegar • T hoje • M mensal • S semanal</span>
            </div>

            {/* Cabeçalhos dos dias da semana */}
            <div className="grid grid-cols-7 gap-2 text-center mb-2">
                {weekdays.map((w) => (<div key={w} className="text-[11px] sm:text-xs font-semibold text-gray-600 uppercase tracking-wide">{w}</div>))}
            </div>

            {/* Grid */}
            <div role="grid" aria-label={viewMode === "monthly" ? "Calendário mensal" : "Calendário semanal"} className="grid grid-cols-7 gap-2" style={{ height: gridHeight, gridAutoRows: `${rowHeight}px` }}>
                {gridDays.map((d) => {
                    const ymd = toYmd(d);
                    const summary = summaryByDate[ymd];
                    const inMonth = isSameMonth(d, referenceMonth);
                    const isToday = ymd === todayYmd;
                    const disabled = !summary || !hasAnyClub;

                    return (
                        <button
                            key={ymd}
                            onClick={(e) => { if (disabled) return; setSelectedDate(ymd); setLastActiveButton(e.currentTarget); }}
                            className={[
                                "relative border p-1 sm:p-2 rounded-xl transition focus:outline-none focus:ring-2 focus:ring-blue-500 h-full",
                                viewMode === "monthly" ? (inMonth ? "bg-white" : "bg-gray-50") : "bg-white",
                                disabled ? "opacity-60 cursor-default" : "hover:shadow-md cursor-pointer",
                                cellTone(summary?.matchesCount),
                                isToday ? "ring-1 ring-blue-300" : "",
                                "overflow-hidden w-full flex flex-col"
                            ].join(" ")}
                            aria-disabled={disabled}
                            aria-pressed={selectedDate === ymd}
                            aria-label={`${ptDay.format(d)} ${ptMonth.format(d)} - ${summary ? `${summary.matchesCount} jogo(s)` : "sem jogos"}`}
                            title={summary ? `${summary.matchesCount} jogo(s)` : "Sem jogos"}
                        >
                            <div className="flex items-start justify-between min-w-0">
                                <span className={`font-semibold ${inMonth ? "text-gray-900" : "text-gray-400"} text-xs sm:text-sm`}>
                                    {ptDay.format(d)}{isToday ? " • hoje" : ""}
                                </span>
                                {loadingMonth && !monthData && <Skeleton className="w-6 sm:w-8 h-3 sm:h-4" />}
                            </div>

                            {summary && hasAnyClub && (
                                <div className="flex-1 flex items-center justify-center">
                                    <div className="sm:hidden text-[10px] text-gray-700 leading-3 text-center">
                                        <div className="font-medium">{summary.matchesCount} jogo(s)</div>
                                        <div className="mt-1 flex items-center justify-center gap-1">
                                            <span className="px-1 rounded bg-green-100 text-green-700">V {summary.wins}</span>
                                            <span className="px-1 rounded bg-yellow-100 text-yellow-700">E {summary.draws}</span>
                                            <span className="px-1 rounded bg-red-100 text-red-700">D {summary.losses}</span>
                                        </div>
                                    </div>

                                    <div className="hidden sm:block text-[11px] text-gray-700 text-center">
                                        <div className="text-xs">{summary.matchesCount} jogo(s)</div>
                                        <div className="mt-1 flex items-center justify-center gap-1">
                                            <span className="px-1.5 py-0.5 rounded bg-green-100 text-green-700">V {summary.wins}</span>
                                            <span className="px-1.5 py-0.5 rounded bg-yellow-100 text-yellow-700">E {summary.draws}</span>
                                            <span className="px-1.5 py-0.5 rounded bg-red-100 text-red-700">D {summary.losses}</span>
                                        </div>
                                        <div className="mt-1">GP {summary.goalsFor} • GC {summary.goalsAgainst}</div>
                                    </div>
                                </div>
                            )}

                            {summary?.matchesCount ? <Dots count={summary.matchesCount} /> : null}
                        </button>
                    );
                })}
            </div>

            {/* Estados */}
            {errorMonth && (
                <div className="mt-4 p-3 bg-red-50 text-red-700 rounded border border-red-200 flex items-center justify-between">
                    <span>{errorMonth}</span>
                    <button className="px-2 py-1 text-sm rounded border" onClick={() => { delete monthCacheRef.current[monthKey]; setReferenceMonth(new Date(referenceMonth)); }}>
                        Tentar novamente
                    </button>
                </div>
            )}
            {!loadingMonth && monthData && monthData.days.length === 0 && (
                <div className="mt-4 p-3 bg-gray-50 text-gray-700 rounded border">Sem jogos neste mês.</div>
            )}

            {/* Drawer do dia */}
            {selectedDate && (
                <div className="fixed inset-0 z-40" role="dialog" aria-modal="true" aria-labelledby={dialogTitleId}>
                    <div className="absolute inset-0 bg-black/30" onClick={() => { setSelectedDate(null); setDayData(null); lastActiveButton?.focus(); }} />
                    <div className="absolute right-0 top-0 h-full w-full sm:w-[560px] bg-white shadow-xl p-4 overflow-y-auto">
                        <div className="flex items-center justify-between mb-3">
                            <div>
                                <h2 id={dialogTitleId} className="text-xl font-semibold">{ptDay.format(fromYmd(selectedDate))} {ptMonth.format(fromYmd(selectedDate))}</h2>
                                {dayData && (
                                    <p className="text-sm text-gray-600">
                                        {dayData.totalMatches} jogo(s) • GP {dayData.goalsFor} • GC {dayData.goalsAgainst} •
                                        <span className="ml-1">V {dayData.wins}</span>
                                        <span className="ml-1">E {dayData.draws}</span>
                                        <span className="ml-1">D {dayData.losses}</span>
                                    </p>
                                )}
                            </div>
                            <button className="px-3 py-2 rounded-lg border hover:bg-gray-50" onClick={() => { setSelectedDate(null); setDayData(null); lastActiveButton?.focus(); }}>Fechar</button>
                        </div>

                        {loadingDay && (<div className="space-y-3" aria-live="polite"><Skeleton className="h-16" /><Skeleton className="h-16" /><Skeleton className="h-16" /></div>)}

                        {errorDay && (
                            <div className="p-3 bg-red-50 text-red-700 rounded border border-red-200 flex items-center justify-between">
                                <span>{errorDay}</span>
                                <button className="px-2 py-1 text-sm rounded border" onClick={() => { if (!selectedDate) return; const cacheKey = `${selectedDate}|${clubKey}`; delete dayCacheRef.current[cacheKey]; setDayData(null); setSelectedDate(selectedDate); }}>
                                    Tentar novamente
                                </button>
                            </div>
                        )}

                        {!loadingDay && dayData && dayData.matches.length === 0 && (<div className="p-3 bg-gray-50 text-gray-700 rounded border">Nenhuma partida neste dia.</div>)}

                        {!loadingDay && dayData && dayData.matches.length > 0 && (
                            <div className="space-y-3">
                                {dayData.matches.map((m) => {
                                    const kickoff = m.timestamp ? ptTime.format(new Date(m.timestamp)) : "";
                                    return (
                                        <div key={m.matchId} className="border rounded-xl p-3 hover:shadow">
                                            <div className="flex items-center justify-between mb-2">
                                                <div className="flex items-center gap-2 min-w-0">
                                                    <Crest id={m.clubACrestAssetId} alt={m.clubAName} />
                                                    <span className="font-medium truncate max-w-[34%]" title={m.clubAName}>{m.clubAName}</span>
                                                    <span className="font-semibold">{m.clubAGoals}</span>
                                                    <span className="text-gray-400">x</span>
                                                    <span className="font-semibold">{m.clubBGoals}</span>
                                                    <span className="font-medium truncate max-w-[34%]" title={m.clubBName}>{m.clubBName}</span>
                                                    <Crest id={m.clubBCrestAssetId} alt={m.clubBName} />
                                                </div>
                                                <div className="flex items-center gap-2">
                                                    {kickoff && <span className="text-xs text-gray-500">{kickoff}</span>}
                                                    <ResultPill r={m.resultForClub} />
                                                </div>
                                            </div>

                                            <div className="grid grid-cols-2 sm:grid-cols-3 gap-2 text-xs text-gray-700">
                                                <div className="bg-gray-50 rounded p-2 flex items-center justify-between"><span>Chutes</span><span className="font-semibold">{m.stats.totalShots}</span></div>
                                                <div className="bg-gray-50 rounded p-2 flex items-center justify-between"><span>Gols</span><span className="font-semibold">{m.stats.totalGoals}</span></div>
                                                <div className="bg-gray-50 rounded p-2 flex items-center justify-between"><span>Passes Certos</span><span className="font-semibold">{m.stats.totalPassesMade}</span></div>
                                                <div className="bg-gray-50 rounded p-2 flex items-center justify-between"><span>Passes (%)</span><span className="font-semibold">{m.stats.passAccuracyPercent.toFixed(0)}%</span></div>
                                                <div className="bg-gray-50 rounded p-2 flex items-center justify-between"><span>Desarmes (%)</span><span className="font-semibold">{m.stats.tackleSuccessPercent.toFixed(0)}%</span></div>
                                                <div className="bg-gray-50 rounded p-2 flex items-center justify-between"><span>Nota Média</span><span className="font-semibold">{m.stats.avgRating.toFixed(2)}</span></div>
                                            </div>

                                            <div className="mt-2 text-right"><Link to={`/match/${m.matchId}`} className="text-sm text-blue-700 hover:underline">Ver detalhes da partida</Link></div>
                                        </div>
                                    );
                                })}
                            </div>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
}
